import React from "react"

const Course = (props) => {
  return(
    <>
      <Header courseName={props.course.name} />
      <Content course={props.course} />
      <Total course={props.course} />
    </>
  );
}

const Header = ( props ) => {
  return (
    <h1>{props.courseName}</h1>
  )
}

const Total = ({ course }) => {
  const sum = course.parts.reduce((runningTotal, currentValue) => runningTotal + currentValue.exercises, 0)
  return(
    <b>Number of exercises: {sum}</b>
  ) 
}

const Content = ({ course }) => {
  return (
    <div>
      {course.parts.map( (val, index) => <Part part={val} key={index}/>)}
    </div>
  )
}

const Part = (props) => {
  return (
    <p>
      {props.part.name} {props.part.exercises}
    </p>    
  )
}


export default Course
