import React, { useState } from 'react'
import AppPart1 from "./part-1/AppPart1"
import AppPart2 from "./part-2/AppPart2"
import AppPart3 from "./part-3/AppPart3"
import AppPart4 from "./part-4/AppPart4"
import AppPart5 from "./part-5/AppPart5"
import AppPart6 from "./part-6/AppPart6"
import AppPart7 from "./part-7/AppPart7"
import AppPart8 from "./part-8/AppPart8"
import AppPart9 from "./part-9/AppPart9"
import AppPart10 from "./part-10/AppPart10"
import AppPart11 from "./part-11/AppPart11"

function App() {
  const [contentID, setContentID] = useState(0);
  return(
    <>
    {
      ([...Array(11).keys()]).map( (val, i) => (
        <button onClick={() => setContentID(i)} key={i}>Part {val + 1}</button>
      ))
    }
      <br/>
      <Content contentID={contentID}/>
    </>
  )
}


const Content = (props) => {
    let contents = [<AppPart1/>, <AppPart2/>, <AppPart3/>, <AppPart4/>, <AppPart5/>, <AppPart6/>, <AppPart7/>, <AppPart8/>, <AppPart9/>, <AppPart10/>, <AppPart11/>];

    let showing = contents[props.contentID]

    return (
        <div> {showing} </div>
    )
}


export default App
