import React, { useState } from 'react'

const App = () => {
  // save clicks of each button to its own state
  const [good, setGood] = useState(0)
  const [neutral, setNeutral] = useState(0)
  const [bad, setBad] = useState(0)
  const [total, setTotal] = useState(0) // extra var for ease of following
  

  function updateRatings(goodnessScore) {
    if (goodnessScore >= 1) setGood(good + 1)
    if (goodnessScore === 0) setNeutral(neutral + 1)
    if (goodnessScore <= -1) setBad(bad + 1)
    setTotal(total + 1)
  }

  return (
    <div>
      <h2>Give feedback</h2>
      {/* not quite sure if i am doing these buttons correctly */}
      <div onClick={() => updateRatings(1)}>
        <Button text={"Set Good :)"} />
      </div>
      <div onClick={() => updateRatings(0)}>
        <Button text={"Set neutral :/"}/>
      </div>
      <div onClick={() => updateRatings(-1)}>
        <Button text={"Set bad :("}/>
      </div>
      <h2>stats</h2>
        <Statistics good={good} neutral={neutral} bad={bad} total={total} />
    </div>
  )
}

const Statistics = (props) => {
  if (props.total === 0) return <p>no feedback yet :(</p>
  return (
    <table>
      <tbody>
        <Statistic text="good" value ={props.good} />
        <Statistic text="neutral" value ={props.neutral} />
        <Statistic text="bad" value ={props.bad} />
        <Statistic text="total" value ={props.total} />
        <Statistic text="average" value ={((props.good  - props.bad)/ props.total).toFixed(2)} />
        <Statistic text="positive %" value ={(100 * props.good / props.total).toFixed(0)} />
      </tbody>
    </table>
  )
}

const Statistic = (props) => {
  return(
    <tr>
      <td>{props.text} </td>
      <td> {props.value} <br/> </td>
    </tr>
  )
}

const Button = (props) => {
  return(
    <button> {props.text} </button>
  )
}


export default App
