import React, { useState, useEffect } from 'react'
import personService from './persons'
import "./styles.css"

const App = () => {
  const [persons, setPersons] = useState([])

  useEffect(() => {
    setPersonDataFromDB()
  }, [])
  
  function setPersonDataFromDB() {
    console.log("getting persons...")
    personService.getAll()
      .then(response => {
        setPersons(response.data)
        console.log(response)
    })  
  }

  const [newName, setNewName] = useState("")
  const [newPhoneNumber, setNewPhoneNumber] = useState("")
  const [search, setSearch] = useState("")
  const [notification, setNotification] = useState(null)

  function setNotificationAndTimeout(message, error=false, timeout=7500) {
    // console.log("ERROR", message, error);
    
    setNotification({message: message, error: error})
    setTimeout(() => {
      console.log("timed out", timeout);
      setNotification(null)
    }, timeout)
    
  }

  function updateOnePersonInList(newPerson) {
    let list = persons.filter(person => person.id !== newPerson.id)
    setPersons([...list, newPerson])
  }

  function addPersonToDB(event) {
    event.preventDefault();

    for (const personIndex in persons) {
      const person = persons[personIndex]
      if (person.name.toLowerCase() !== newName.toLowerCase()) continue
      // database doesn't contain the name we're looking for
      console.log(person);
      if (!window.confirm((`${newName} is already added to phonebook. replace old number with a new one?`))) return
      
      const updatedPersonObject = {...person, number: newPhoneNumber}
      personService.update(person.id, updatedPersonObject)
      updateOnePersonInList(updatedPersonObject)
      setNotificationAndTimeout("Updated " + person.name)
      return
    }
    if (newName === "" || newPhoneNumber === "") {
      alert(`name and/or phone number cannot be empty!`)
      return
    }
    
    const submittableObject = getCurrentPersonObject()
    personService.create(submittableObject) 
      .then(response => {
        console.log(response.data)
        setPersons([...persons, response.data])
      })
    setNotificationAndTimeout("Added " + submittableObject.name)
    setNewName("")
    setNewPhoneNumber("")
  }

  function removePerson(event, person) {
    event.preventDefault();
    const id = person.id;
    console.log(id);
    if (!window.confirm("Do you really want to delete this poor soul of a person")) return
    setNotificationAndTimeout("removed " + person.name)
    personService.deleteByID(id)
      .catch(error => {setNotificationAndTimeout("oh no! an error occured! :( the error says: " + error.name + ": " + error.message, true);
            console.log(error);} )
    setPersons(persons.filter(person => person.id !== id))
  }

  function handleNameChange(event) {
    const newVal = event.target ? event.target.value : "";
    setNewName(newVal);
  }

  function handlePhoneNumberChange(event) {
    // not sure if phone number should only have numbers so i'll allow everything until stated otherwise
    const newVal = event.target ? event.target.value : "";
    setNewPhoneNumber(newVal);
  }

  function handleSearchChange(event) {
    const newVal = event.target ? event.target.value : "";
    setSearch(newVal)
  }

  function getCurrentPersonObject() {
    let minId = 1
    let idList = persons.map(i => i.id)
    for (let i = minId; i < persons.length + 10; i++) {
      if (!idList.includes(i)) {
        // O(n^2), could be optimised?
        minId = i;
        break
      }
    }
    return { name: newName, number: newPhoneNumber, id: minId }
  }

  return (
    <div>
      <h2>Phonebook</h2>

      <Filter 
        search={search} 
        handleSearchChange={handleSearchChange}
      />
      <h3>Add new name</h3>
      
      <PersonForm 
        handleSubmit={addPersonToDB} 
        newName={newName} 
        handleNameChange={handleNameChange} 
        newPhoneNumber={newPhoneNumber} 
        handlePhoneNumberChange={handlePhoneNumberChange}
      />
      <h3>Numbers</h3>
      <Notification notification={notification}/>
      <Numbers persons={persons} handleClick={removePerson} search={search}/>
    </div>
  )
}

const Filter = (props) => {
  return (
    <>
      look for a name: 
      <input value={props.search} onChange={props.handleSearchChange} />
    </>
  )
}

const PersonForm = (props) => {
  return (
    <>
      <form onSubmit={props.handleSubmit}>
        <div>
          name: <input value={props.newName} onChange={props.handleNameChange} />
        </div>
        <div>
          number: <input value={props.newPhoneNumber} onChange={props.handlePhoneNumberChange} />
        </div>
        <div>
          <button type="submit">add</button>
        </div>
      </form>
    </>
  )
}

const Notification = (props) => {
  const notification = props.notification
  if (notification === null || notification === "" || !notification) {
    return null
  }
  let classStrName = notification.error ? "notification error" : "notification"

  return (
    <div className={classStrName}>
      {notification.message}
      <br/>
    </div>
  )
}

const Numbers = (props) => {
  const filteredNames = props.persons.filter( person => person.name.toLowerCase().includes( props.search.toLowerCase() ) )
  return(
    <>
      {filteredNames.map((value, index) => 
      <div key={index}>
        {value.name}: {value.number}
        <button onClick={(e) => props.handleClick(e, value)} >kill this person</button>
      </div>)}
    </>
  )
}

export default App
