import React, { useState } from 'react'
import AppEx1 from "./courseinfo/AppEx1"
import AppEx2 from "./unicafe/AppEx2"
import AppEx3 from "./anecdotes/AppEx3"

function App() {
  const [contentID, setContentID] = useState(0);

  return(
    <>
      <button onClick={() => setContentID(0)}>course info</button>
      <button onClick={() => setContentID(1)}>unicafe</button>
      <button onClick={() => setContentID(2)}>anecdotes</button>
      <br/>
      <Content contentID={contentID}/>
    </>
  )
}


const Content = (props) => {
    let contents = [<AppEx1/>, <AppEx2/>, <AppEx3/>];

    let showing = contents[props.contentID]

    return (
        <div> {showing} </div>
    )
}


export default App
