import React, { useState, useEffect } from 'react'
import axios from "axios"

const App = () => {

  const [countries, setCountries] = useState([])

  useEffect(() => {
    axios
      .get('https://restcountries.eu/rest/v2/all')
      .then(response => {
        setCountries(response.data)
      })
    }, []
  )

  const [availableSearches, setAvailableSearches] = useState([]);
  function handleSearchChange(event) {
    const filteredCountries = countries.filter(country => {
      return country.name.toLowerCase().includes(event.target.value.toLowerCase())
    }
  )
  setAvailableSearches(filteredCountries);
  }
  
  return (
    <>
      look for a country:
      <input onChange={handleSearchChange} />
      <br/>
      <CountryInfo filteredCountries={availableSearches} />
    </>
  )
}

const CountryInfo = (props) => {
  const filteredCountries = props.filteredCountries
  if (filteredCountries.length === 0) return <>no data found :(</>

  if (filteredCountries.length > 10) return <>Too much data to process! Try narrowing your search terms!</>

  if (filteredCountries.length > 1) return (
    <>
      {filteredCountries.map( (val, index) => <div key={index}>{val.name}</div>)}
    </>
  )
  const country = filteredCountries[0]
  return (
    <>
      <h2>{country.name}</h2>
      <CountryAltSpellings country={country}/>
      <h3>capital: </h3>{country.capital}
      <h3>flag: </h3><img src={country.flag} alt="" style={{height: "100px"}}/>
      <CountryCurrencies country={country}/>
      <CountryWeather country={country}/>
    </>
  )
}

const CountryAltSpellings = (props) => {
  if (props.country.altSpellings.length > 0) {
    return (
    <>
      <h3>also knows as: </h3>{props.country.altSpellings.map( (val, index) => <div key={index}>{val}</div> )}
    </>
  )}
  return <></>
}

const CountryCurrencies = (props) => {
  if (props.country.currencies.length > 0) return (
    <>
      <h3>currencies: </h3>{props.country.currencies.map( (val, index) => <div key={index}>{val.name}</div> )}
    </>
  )
  return <></>
}

const CountryWeather = (props) => {
  const apiKey = process.env.REACT_APP_API_KEY
  // using openweathermap.org
  function getApiCallURL() {
    if (!props.country.capital) return ""
    return "https://api.openweathermap.org/data/2.5/weather?q=" + props.country.capital + "&APPID=" + apiKey;
  }

  const [weatherData, setWeatherData] = useState()

  function countryData() {
    console.log("fetching data...", new Date());
    fetch(getApiCallURL())
      .then(res => res.json())
      .then(
        (result) => {
          setWeatherData(result)
        },
        (error) => {
          console.log("error");
    });
  }
  
  function countryDataLimitedAPIUsage() {
    let now = new Date().getTime(); // number of milliseconds since the 60's ended in London
    const oneMinute = 1000 * 60;

    if (localStorage.weather && localStorage.expireTime && parseInt(localStorage.expireTime) > now) {
      console.log("visit was too recent");
      return (<>
        <h3>current temperature at capital: </h3> Limiting API usage :(
      </>)
    }
    else {
      let data = countryData();
      let newExpireTime = now + oneMinute;
      localStorage.setItem("weather", data);
      localStorage.setItem("expireTime", newExpireTime);

      console.log(`First visit (since storage was cleared).`);
      setWeatherData(data);
    }
  }
  countryDataLimitedAPIUsage()
  if (typeof weatherData != "undefined") {
    return (<>
      <h3>current temperature at capital: </h3> {(weatherData.main.temp - 273.15).toFixed(0)}Â°C
    </>)
  }
  else return <>Api usage is too high... </>
}

export default App
